.calculator-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 1rem;
  background-color: #121212;
}

.calculator-card {
  background: #121212;
  padding: 1rem;
  border-radius: 8px;
  width: 100%;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

h1 {
  text-align: center;
  color: #ffffff;
  margin-bottom: 1.5rem;
  font-size: 1.4rem;
  font-weight: 500;
}

.semester-container {
  background: #1a1a1a;
  padding: 1rem;
  border-radius: 6px;
  margin-bottom: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.semester-header h2 {
  font-size: 1.1rem;
  color: #ffffff;
  font-weight: 500;
}

.subject-header {
  color: #ffffff;
  opacity: 0.7;
  font-size: 0.9rem;
}

.subject-row input {
  background-color: #2a2a2a;
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 8px;
  border-radius: 4px;
}

.subject-row input:focus {
  border-color: #ffffff;
  background-color: #333333;
}

.action-btn {
  padding: 10px;
  border-radius: 4px;
  font-weight: 500;
  width: 100%;
  margin: 0.5rem 0;
}

.add-btn {
  background-color: #333333;
  color: #ffffff;
}

.add-btn:hover {
  background-color: #404040;
}

.calculate-btn {
  background-color: #ffffff;
  color: #000000;
}

.calculate-btn:hover {
  background-color: #e0e0e0;
}

.reset-btn {
  background-color: #333333;
  color: #ffffff;
}

.reset-btn:hover {
  background-color: #404040;
}

.remove-btn {
  width: 28px;
  height: 28px;
  background-color: #333333;
  color: #ffffff;
  border: none;
  border-radius: 4px;
}

.remove-btn:hover:not(:disabled) {
  background-color: #404040;
}

.remove-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.semester-result {
  background-color: #1a1a1a;
  color: #ffffff;
  padding: 0.8rem;
  border-radius: 4px;
  margin-top: 1rem;
}

.overall-result {
  background-color: #ffffff;
  color: #000000;
  padding: 1rem;
  border-radius: 4px;
  margin-top: 1rem;
  text-align: center;
}

@media (max-width: 600px) {
  .calculator-container {
    padding: 0.5rem;
    align-items: flex-start;
  }
  
  .calculator-card {
    padding: 0.8rem;
  }
  
  .subject-row {
    grid-template-columns: 1.2fr 0.8fr 0.6fr 28px;
    gap: 4px;
  }

  .subject-header {
    grid-template-columns: 1.2fr 0.8fr 0.6fr 28px;
    font-size: 0.8rem;
  }

  .subject-row input {
    font-size: 0.9rem;
  }

  .button-group {
    gap: 6px;
  }

  .action-btn {
    padding: 10px;
  }
}

@supports (padding: max(0px)) {
  .calculator-container {
    padding: max(0.5rem, env(safe-area-inset-left));
  }
}